import React, { useEffect } from 'react';
import Color from 'color';
import { graphql } from 'gatsby';
import { fade } from '@material-ui/core/styles/colorManipulator';
import MuiAvatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import CardHeader from '@material-ui/core/CardHeader';
import { css } from '@emotion/core';
import styled from '@theme';
import { default as Video } from '@src/components/Video';
import { default as Layout } from '@src/components/layout';

const Container = styled.main`
    /* display: flex; */
    padding: 20px;
    width: 100%;
    max-width: 700px;
    word-break: word-wrap;

    .banner-img-for-preview {
        display: block;
    }
`;

const Avatar = styled(MuiAvatar)`
    width: ${(props) => props.theme.spacing(10)}px;
    height: ${(props) => props.theme.spacing(10)}px;
`;

const BlogContentWrapper = styled.article`
    .gatsby-resp-image-background-image {
        margin-top: 20px !important;
        margin-bottom: 20px !important;
    }

    h1 {
        ${(props) => props.theme.typography.h4 as any};
    }

    h2 {
        ${(props) => props.theme.typography.h5 as any};
    }

    h3 {
        ${(props) => props.theme.typography.h6 as any};
    }

    p {
        ${(props) => props.theme.typography.body2 as any};
        line-height: 1.75rem;
    }

    img {
        max-width: 700px;
        box-shadow: ${(props) => props.theme.shadows[4]} !important;
        border-radius: 10px;
        :hover {
            box-shadow: ${(props) => props.theme.shadows[10]} !important;
        }
    }

    code {
        color: ${(props) => props.theme.palette.primary.main};
        border: 1px solid rgba(0, 0, 0, 0.3);
        border-radius: 3px;
        padding: 3px;
    }

    pre {
        box-shadow: ${(props) => props.theme.shadows[10]};
        code {
            color: inherit;
            border: none;
            border-radius: unset;
            padding: unset;
        }
    }

    blockquote {
        background-color: ${(props) => Color(props.theme.palette.secondary.light).alpha(0.2).string()};
        border-left-color: ${(props) => props.theme.palette.secondary.light};
        border-left-width: 9px;
        border-left-style: solid;
        padding: 10px;
        margin-bottom: 30px;
        margin-top: 20px;
        margin-left: 0px;
        margin-right: -8px;
    }

    a {
        color: ${(props) => props.theme.palette.info.main};
        &:hover {
            color: ${(props) => props.theme.palette.info.light};
        }
    }

    li {
        a {
            color: ${(props) => props.theme.palette.info.main};
            &:hover {
                color: ${(props) => props.theme.palette.info.light};
            }
        }
    }
`;

const TocWrapper = styled.nav`
    ${(props) => props.theme.breakpoints.down('md')} {
        display: none;
    }

    box-shadow: ${(props) => props.theme.shadows[10]};
    position: absolute;
    left: calc(50% + 150px);
    top: 20%;
    display: flex;
    flex-direction: column;
    border-radius: 0.25rem;
    padding: 2rem;
    margin-top: ${(props) => props.theme.spacing(4)}px;
    overflow: auto;
    a,
    ul,
    ol,
    li {
        text-decoration: none;
        list-style-type: none;
        &:visited,
        :link,
        :active {
            color: inherit;
        }
    }

    li {
        margin-top: ${(props) => props.theme.spacing(1)}px;
        margin-bottom: ${(props) => props.theme.spacing(1)}px;
    }

    ul {
        padding-inline-start: ${(props) => props.theme.spacing(2)}px;
        ${({ theme }) => {
            const borderColor = Color(theme.palette.common.black).alpha(0.3).toString();
            return css`
                border-left: 1px dashed ${borderColor};
                margin-left: 20px;
            `;
        }}
    }

    a {
        display: inline-block;
        padding: ${(props) => props.theme.spacing(1)}px;
        ${(props) => props.theme.typography.button as any};
        text-transform: inherit;
        &:hover {
            text-decoration: none;
            background-color: ${(props) =>
                fade(props.theme.palette.text.primary, props.theme.palette.action.hoverOpacity)};
        }
    }

    &:first-of-type {
        & > ul {
            border-left: none;
            margin-left: 0;
            padding-inline-start: 0;
        }
    }
`;

export default function BlogTemplate({
    data, // this prop will be injected by the GraphQL query below.
}) {
    const { markdownRemark } = data; // data.markdownRemark holds your post data
    const { frontmatter, html, tableOfContents, fields } = markdownRemark;

    const handleScrollToFix = (): void => {
        window.scrollTo(window.scrollX, window.scrollY - 100);
    };

    useEffect(() => {
        if (typeof window !== `undefined`) {
            addEventListener('hashchange', handleScrollToFix);
            return () => removeEventListener('hashchange', handleScrollToFix);
        }
    }, []);

    return (
        <Layout
            title={frontmatter.title}
            ogImage={frontmatter?.bannerPhoto?.childImageSharp?.fluid?.src}
            slug={fields.slug}
        >
            <Container>
                <div>
                    <Typography variant="h4">{frontmatter.title}</Typography>
                    <CardHeader
                        avatar={
                            <Avatar
                                src={frontmatter?.authorPhoto?.childImageSharp?.fluid?.src || null}
                                alt={frontmatter?.authorPhoto?.childImageSharp?.fluid?.originalName || null}
                            />
                        }
                        title={frontmatter.author}
                        subheader={frontmatter.date}
                    />
                    {frontmatter?.youtubePath && <Video src={frontmatter.youtubePath} title={frontmatter.title} />}
                </div>
                <hr />
                {html && <BlogContentWrapper dangerouslySetInnerHTML={{ __html: html }} />}
                {tableOfContents && <TocWrapper dangerouslySetInnerHTML={{ __html: tableOfContents }}></TocWrapper>}
            </Container>
        </Layout>
    );
}
export const pageQuery = graphql`
    query($slug: String!) {
        markdownRemark(fields: { slug: { eq: $slug } }) {
            html
            tableOfContents(absolute: false)
            frontmatter {
                date(formatString: "YYYY/MM/DD")
                title
                category
                tags
                author
                authorPhoto {
                    childImageSharp {
                        fluid {
                            src
                            originalName
                        }
                    }
                }
                bannerPhoto {
                    childImageSharp {
                        fluid {
                            src
                            originalName
                        }
                    }
                }
                youtubePath
            }
            fields {
                slug
            }
        }
    }
`;
